import { useState } from 'react';

export default function Navbar() {
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

    const toggleMobileMenu = () => {
        setIsMobileMenuOpen(!isMobileMenuOpen);
    };

    return (
        <nav className="navbar">
            <div className="navbar-container">
                {/* Brand Logo */}
                <div className="navbar-brand">
                    <a href="/">KDGO</a>
                </div>

                {/* Desktop Navigation Links */}
                <div className="navbar-links">
                    <a href="/projects" className="nav-link">Projects</a>
                    <a href="/updates" className="nav-link">Updates</a>
                    <a href="/team" className="nav-link">Team</a>
                    <div className="nav-dropdown">
                        <a href="#" className="nav-link dropdown-toggle">
                            More
                            <svg className="dropdown-icon" width="12" height="8" viewBox="0 0 12 8" fill="none">
                                <path d="M1 1.5L6 6.5L11 1.5" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round"/>
                            </svg>
                        </a>
                    </div>
                </div>

                {/* Desktop Action Buttons */}
                <div className="navbar-actions">
                    <button className="btn-login">Login</button>
                    <button className="btn-explore">Explore</button>
                </div>

                {/* Mobile Menu Button */}
                <button 
                    className={`mobile-menu-toggle ${isMobileMenuOpen ? 'active' : ''}`}
                    onClick={toggleMobileMenu}
                    aria-label="Toggle mobile menu"
                >
                    <span className="hamburger-line"></span>
                    <span className="hamburger-line"></span>
                    <span className="hamburger-line"></span>
                </button>
            </div>

            {/* Mobile Menu */}
            <div className={`mobile-menu ${isMobileMenuOpen ? 'active' : ''}`}>
                <div className="mobile-menu-content">
                    <a href="/projects" className="mobile-nav-link" onClick={toggleMobileMenu}>Projects</a>
                    <a href="/updates" className="mobile-nav-link" onClick={toggleMobileMenu}>Updates</a>
                    <a href="/team" className="mobile-nav-link" onClick={toggleMobileMenu}>Team</a>
                    <a href="#" className="mobile-nav-link" onClick={toggleMobileMenu}>More</a>
                    <div className="mobile-menu-actions">
                        <button className="btn-login-mobile">Login</button>
                        <button className="btn-explore-mobile">Explore</button>
                    </div>
                </div>
            </div>
        </nav>
    );
}
